@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  @include breakpoint($phone2) {
    padding: 20px 0;
  }
  

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;
    align-items: center;

    img {
      margin-right: 9px;
      @include breakpoint($phone2) {
        margin-right: 5px;
        height: 40px;
        width: 49px;
      }
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
      @include breakpoint($phone2) {
        display: none;
      }
    }
  }
}
